import React from 'react';

function FilterPanel(props) {

    function _showFilters(){
        document.getElementsByClassName('filter-container')[0].classList.toggle('hide');
    }
    return (
        <div className='filter'>
            <header className='filter-header' onClick={_showFilters}>
                <b>Filtrar por:</b>
            </header>

            <div className='filter-container hide'>
                <div>
                    <b className='filter-title'>Marcas:</b>
                    <br />
                    <ul className='list'>
                        <li>marca 1</li>
                        <li>marca 2</li>
                        <li>marca 3</li>
                        <li>marca 4</li>
                        <li>maraca 5</li>
                    </ul>
                </div>
                {props.path === 'lentesdecontacto' ? 
                    <div className='lefilter'>
                        <div>
                            <b className='filter-title'>Laboratorios:</b>
                            <br />
                            <ul className='list'>
                                <li>laboratorio 1</li>
                                <li>laboratorio 2</li>
                                <li>laboratorio 3</li>
                                <li>laboratorio 4</li>
                                <li>laboratorio 5</li>
                            </ul>
                        </div>
                        <div>
                            <b className='filter-title'>Condicion visual:</b>
                            <br />
                            <ul className='list'>
                                <li>Condicion visual 1</li>
                                <li>Condicion visual 2</li>
                                <li>Condicion visual 3</li>
                                <li>Condicion visual 4</li>
                                <li>Condicion visual 5</li>
                            </ul>
                        </div>
                    </div>
                    :
                    null
                }
            </div>
        </div>
    )
}

export default FilterPanel